@import url(../App.css);

.navigation {
    width: calc(100% - 60px);
    height: min-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    height: 57px;
    top: 0;
    position: relative;
    z-index: 100;
    padding-inline: 30px;
    font-family: var(--primaryfont);
}

#logoMenuIcon {
    width: 1.5vw;
    aspect-ratio: 1 / 1;
}


.menu-button {
    display: none;
}

.navigasi-menu {
    list-style: none;
    padding: 0px;
    display: flex;
    gap: 30px;
    transition: 0.5s;
    margin-top: 0px;
    margin: 0;
}

.arrow {
    font-size: 10px;
}

.navigasi-menu li {
    font-size: 10px;
    margin-block: 3px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 104px;
    width: 100%;
    border-bottom: 1px solid var(--border);
    transition: 0.2s;
}

.navigasi-menu:hover li {
    opacity: 0.5;

}



.navigasi-menu:hover li:hover {
    opacity: 1;
    background-color: var(--border);
}

.navigasi-menu li a {
    display: flex;
    justify-content: space-between;
    color: var(--button);
    align-items: center;
    min-width: 104px;
    width: 100%;
}


.theme {
    background-color: transparent;
    color: var(--primary);
    font-weight: 800;
    font-size: 13px;
    width: 40px;
    height: 20px;
    display: grid;
    place-items: center;
    transition: 0.3s;
    border: 1px solid var(--border);
    background-color: var(--border);
    cursor: pointer;
    transition: 0.5s;
    position: relative;
    
}

.theme2 {
    display: none;
    visibility: hidden;
}

.theme i {
    transform: translateY(1px);
}

#swchbtn1,
#swchbtn2 {
    background-color: var(--button);
    height: 20px;
    aspect-ratio: 1/1;
    position: absolute;
    transition: 0.3s;
    left: calc(0%);
    display: grid;
    place-items: center;
}

#swchbtn1.switchTheme,
#swchbtn2.switchTheme {
    left: calc(50%);
}

.contact:hover {
    scale: 0.9;
    outline-offset: 0.5em;
    outline: 1px solid var(--button);
}


