.close {
    display: none;
}

.navigasi-menu h1 {
    display: none;
}

@media (max-width:767px) {
    .navigation {
        box-sizing: border-box;
        width: calc(100%);
        height: min-content;
        display: flex;
        flex-direction: column;
        flex-direction: row-reverse;
        justify-content: space-between;
        background-color: rgba(10, 10, 10, 0);
        /* backdrop-filter: blur(10px); */
        align-items: center;
        color: white;
        height: 57px;
        /* position: fixed; */
        top: 0px;
        z-index: 100;
        padding-inline: 30px;
        font-family: var(--primaryfont);
    }

    .navigasi-menu h1 {
        display: block;
        padding-top: 30px;
    }

    .theme {
        background-color: transparent;
        color: var(--primary);
        font-weight: 800;
        font-size: 13px;
        width: 40px;
        height: 20px;
        display: grid;
        place-items: center;
        transition: 0.3s;
        border: 1px solid var(--border);
        background-color: var(--border);
        cursor: pointer;
        transition: 0.5s;
        position: relative;
        overflow: hidden;
    }

    .theme1 {
        display: none;
    }

    .theme2 {

        display: grid;
        visibility: visible;
    }

    .theme i {
        transform: translateY(1px);
    }

    #swchbtn {
        background-color: var(--button);
        height: 20px;
        aspect-ratio: 1/1;
        position: absolute;
        transition: 0.3s;
        left: calc(0%);
        display: grid;
        place-items: center;
    }

    #swchbtn.switchTheme {
        left: calc(50%);
    }

    .section {
        margin-top: 0px;
    }

    .menu-button {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 50px;
        aspect-ratio: 1/1;
        color: var(--button);
    }

    .menu-button i {
        font-size: 1em;
        transform: translateY(3px);
    }

    .contact {
        display: none;
    }

    .navigasi-menu {
        list-style: none;
        padding: 0px;
        position: fixed;
        display: flex;
        flex-direction: column;
        padding-left: 30px;
        padding-right: 30px;
        width: calc(100% - 60px);
        height: 100%;
        padding-top: 87px;
        top: 0;
        left: -100%;
        z-index: 100;
        transition: 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
    }

    .navigasi-menu.open {
        left: 0%;
    }

    .navigasi-menu li {
        font-size: 17px;
        margin-bottom: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        border-bottom: 1px solid var(--border);
    }

    .navigasi-menu h1 {
        font-family: var(--secondfont);
        color: var(--button);
        font-weight: 800;
        font-size: 10vw;
        display: block;
        margin: 0;
        padding-block: 20px;
    }

    .close {
        display: grid;
        place-items: center;
        color: var(--button);
        top: -100%;
        left: 0;
        width: 100%;
        position: fixed;
        z-index: 101;
        height: 86px;
        transition: 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
        border-bottom: 1px solid var(--border);
    }

    .close.open {
        top: 0;
    }

    #thecontent {
        scale: 1;
        transition: 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
        opacity: 1;
    }

    #thecontent.open {
        transform: translateZ(-100%);
        opacity: 0;

    }

    #logoMenuIcon {
        width: 6vw;
        aspect-ratio: 1 / 1;
    }

    :is(#logoMenuIcon, #expandMenuIcon) {
        transition: 0.5s;
        height: 30px;
        display: flex;
        align-items: center;
    }

    :is(#logoMenuIcon, #expandMenuIcon).open {
        transform: translateY(-500%);
    }

}